html {
  font-family: Roboto, Arial, sans-serif;
}

body {
  --active-color: #00acdc;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  display: flex;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-right: 20px;
}

.nav-item:last-child {
  margin-right: 0;
}

.nav-link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav-link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
